import { Component, OnInit } from '@angular/core';
import { MatDialogRef, MatDialog, MatSnackBar } from '@angular/material';
import { Subscription } from 'rxjs/Subscription';

import { AppConfirmService } from '../../../../shared/services/app-confirm/app-confirm.service';
import { AppLoaderService } from '../../../../shared/services/app-loader/app-loader.service';
import { DashboardService }  from '../../../../shared/services/admin/dashboard.service';
import { VideoTablePopupComponent } from './videopopup/video-table-popup.component'

@Component({
  selector: 'app-adminvideos',
  templateUrl: './videos.component.html',
  styleUrls: ['./videos.component.scss']
})
export class AppAdminVideosComponent implements OnInit {

  videos;
  constructor(
    private dialog: MatDialog,
    private snack: MatSnackBar,
    private confirmService: AppConfirmService,
    private loader: AppLoaderService,
    private dashboardService: DashboardService
  ) { }

  ngOnInit() {
    this.loadVideos();
  }

  loadVideos() {
    this.dashboardService.getAllVideos().subscribe(data => {
      this.videos = data;
      this.videos.map(video => {
        var video_id = video.video_link.replace('https://www.youtube.com/watch?v=', '');
        video.video_thumb = 'https://img.youtube.com/vi/' + video_id + '/0.jpg'
      })
    })
  }

  openPopUp(data: any = {}, isNew?) {
    let title = isNew ? 'Add new video' : 'Update video';
    let dialogRef: MatDialogRef<any> = this.dialog.open(VideoTablePopupComponent, {
      width: '720px',
      disableClose: true,
      data: { title: title, payload: data }
    })
    dialogRef.afterClosed()
      .subscribe(res => {
        if(!res) {
          // If user press cancel
          return;
        }
        this.loader.open();
        if (isNew) {
          this.dashboardService.addVideo(res)
            .subscribe(data => {
              this.loader.close();
              this.snack.open('Video Added!', 'OK', { duration: 4000 })
              this.loadVideos();
            })
        } else {
          
          this.dashboardService.updateVideo(data.video_id, res)
            .subscribe(data => {
              this.loader.close();
              this.snack.open('Video Updated!', 'OK', { duration: 4000 })
              this.loadVideos();
            })
        }
      })
  }

  changeFeature(row) {
    this.dashboardService.changeVideoFeature(row.video_id).subscribe((data: any) => {
      // row.is_featured = data.is_featured
      this.loadVideos();
    })
  }

  deleteItem(row) {
    this.confirmService.confirm({message: `Delete?`})
      .subscribe(res => {
        if (res) {
          this.loader.open();
          this.dashboardService.deleteVideo(row.video_id)
            .subscribe(data => {
              this.loader.close();
              this.snack.open('Video deleted!', 'OK', { duration: 4000 });
              this.loadVideos();
            })
        }
      })
  }

}



