import { Component, OnInit } from '@angular/core';
import { MatDialogRef, MatDialog, MatSnackBar } from '@angular/material';
import { Subscription } from 'rxjs/Subscription';

import { AppConfirmService } from '../../../shared/services/app-confirm/app-confirm.service';
import { AppLoaderService } from '../../../shared/services/app-loader/app-loader.service';
import { PlacementService } from '../../../shared/services/admin/placement.service';
import { PlacementTablePopupComponent } from './placementpopup/placement-table-popup.component';

@Component({
  selector: 'app-adminplacement',
  templateUrl: './app-adminplacement.component.html',
  styleUrls: ['./app-adminplacement.component.scss']
})
export class AppAdminPlacementComponent implements OnInit {

  public items: any[];
  public getItemSub: Subscription;

  constructor(
    private dialog: MatDialog,
    private snack: MatSnackBar,
    private confirmService: AppConfirmService,
    private loader: AppLoaderService,
    private placementService: PlacementService
  ) {}

  ngOnInit() {
    this.getItems()
  }
  
  ngOnDestroy() {
    if (this.getItemSub) {
      this.getItemSub.unsubscribe()
    }
  }

  getItems() {
    this.getItemSub = this.placementService.getAll()
      .subscribe((data: any) => {
        this.items = data;
      })
  }

  openPopUp(data: any = {}, isNew?) {
    let title = isNew ? 'Add new Placement' : 'Update Placement';
    let dialogRef: MatDialogRef<any> = this.dialog.open(PlacementTablePopupComponent, {
      width: '720px',
      disableClose: true,
      data: { title: title, payload: data }
    })
    dialogRef.afterClosed()
      .subscribe(res => {
        if(!res) {
          // If user press cancel
          return;
        }
        this.loader.open();
        if (isNew) {
          this.placementService.addItem(res)
            .subscribe(data => {
              this.loader.close();
              this.snack.open('Placement Added!', 'OK', { duration: 4000 })
              this.getItems();
            })
        } else {
         
          this.placementService.updateItem(data.placement_id, res)
            .subscribe(data => {
              this.loader.close();
              this.snack.open('Placement Updated!', 'OK', { duration: 4000 })
              this.getItems();
            })
        }
      })
  }
  
  deleteItem(row) {
    this.confirmService.confirm({message: `Delete?`})
      .subscribe(res => {
        if (res) {
          this.loader.open();
          this.placementService.removeItem(row.placement_id)
            .subscribe(data => {
              this.loader.close();
              this.snack.open('Placement deleted!', 'OK', { duration: 4000 });
              this.getItems();
            })
        }
      })
  }

  changeFeature(row) {
    this.placementService.changeFeature(row.placement_id).subscribe((data: any) => {
      this.getItems();
    })
  }

}



