import { Component, OnInit } from '@angular/core';
import { MatSnackBar } from '@angular/material';
import { Subscription } from 'rxjs/Subscription';
import { Router } from '@angular/router';

import { AppConfirmService } from '../../../shared/services/app-confirm/app-confirm.service';
import { AppLoaderService } from '../../../shared/services/app-loader/app-loader.service';
import { SongsService } from '../../../shared/services/admin/songs.service';

@Component({
  selector: 'app-adminsongs',
  templateUrl: './app-adminsongs.component.html',
  styleUrls: ['./app-adminsongs.component.scss']
})
export class AppAdminSongsComponent implements OnInit {

  public songs: any[];
  public getItemSub: Subscription;

  constructor(
    private songsService: SongsService,
    private router: Router,
    private confirmService: AppConfirmService,
    private loader: AppLoaderService,
    private snack: MatSnackBar
  ) {}

  ngOnInit() {
    this.getItems()
  }
  
  ngOnDestroy() {
    if (this.getItemSub) {
      this.getItemSub.unsubscribe()
    }
  }

  getItems() {
    this.getItemSub = this.songsService.getAll()
      .subscribe((data: any) => {
        this.songs = data;
      })
  }

  gotosong(song_id) {
    this.router.navigate(['/admin/song/' + song_id]);
  }

  deleteItem(row) {
    this.confirmService.confirm({message: `Delete ${row.song_title}?`})
      .subscribe(res => {
        if (res) {
          this.loader.open();
          this.songsService.remove(row.song_id)
            .subscribe(data => {
              this.loader.close();
              this.snack.open('Song deleted!', 'OK', { duration: 4000 });
              this.getItems();
            })
        }
      })
  }

  changeFeature(row) {
    this.songsService.changeFeature(row.song_id).subscribe((data: any) => {
      // row.is_featured = data.is_featured 
      this.getItems();
    })
  }
}



