import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { CustomValidators } from 'ng2-validation';

import { DashboardService } from '../../../../../shared/services/admin/dashboard.service';

@Component({
  selector: 'app-video-table-popup',
  templateUrl: './video-table-popup.component.html',
  styleUrls: ['./video-table-popup.component.scss']
})
export class VideoTablePopupComponent implements OnInit {
  public itemForm: FormGroup;
  is_changed_avatar = false;
  video_thumb = '';
  file;
  isUploading = false;
  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<VideoTablePopupComponent>,
    private fb: FormBuilder,
    private dashboardService: DashboardService
  ) { }

  ngOnInit() {
    this.buildItemForm(this.data.payload)
  }
  
  buildItemForm(item) {
    this.video_thumb = item.video_thumb;
    this.itemForm = this.fb.group({
      video_thumb: [item.video_thumb || ''],
      video_link: [item.video_link || '', [Validators.required, CustomValidators.url]],
      is_featured: [item.is_featured == 0 ? false : true],
    })
  }

  submit() {
    this.dialogRef.close(this.itemForm.value)
  }

  clickChangeAvatar() {
    document.getElementById('avatar-input').click();
  }

  onFileChange(event) {
    
    event.preventDefault();
    if (event.target.files.length > 0) {
      this.is_changed_avatar = true;
      this.file = event.target.files[0];
      var reader = new FileReader();
      reader.onload = function(e: any) {
        var avatarImage : any = document.getElementById('avatar_img');
        avatarImage.src = e.target.result;
      }
      reader.readAsDataURL(this.file);
    } else {
      this.is_changed_avatar = false;
    }
  }

  upload() {
    var me = this;
    this.isUploading = true;
    this.dashboardService.uploadMusicToS3(this.file).on('httpUploadProgress', function (evt) {
        console.log(evt.loaded + ' of ' + evt.total + ' Bytes');
    }).send(function (err, data) {
      
      if (err) {
          console.log('There was an error uploading your file: ', err);
          return false;
      }
      console.log('Successfully uploaded file.', data);
      me.itemForm.controls['video_thumb'].setValue(data['Location']);
      me.is_changed_avatar = false;
      me.file = null;
      me.isUploading = false;
      return true;
    });
  }
}



