import { Component, OnInit } from '@angular/core';
import { MatDialogRef, MatDialog, MatSnackBar } from '@angular/material';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { CustomValidators } from 'ng2-validation';

import { FilterService } from '../../../../shared/filter.service';
import { AppLoaderService } from '../../../../shared/services/app-loader/app-loader.service';
import { AppConfirmService } from '../../../../shared/services/app-confirm/app-confirm.service';
import { FilterPopupComponent } from './filterpopup/filterpopup.component'

@Component({
  selector: 'app-filter',
  templateUrl: './filter.component.html',
  styleUrls: ['./filter.component.scss']
})
export class AppFilterComponent implements OnInit {

  filters;

  constructor(
    private loader: AppLoaderService,
    private snack: MatSnackBar,
    private fb: FormBuilder,
    private dialog: MatDialog,
    private filterService: FilterService,
    private confirmService: AppConfirmService
  ) { }

  ngOnInit() {
    this.loadFilters();
  }

  loadFilters() {
    this.filterService.getAll().subscribe(data => {
      this.filters = data;
      console.log(data)
    })
  }

  deleteItem(id, title, type) {
    this.confirmService.confirm({message: title})
      .subscribe(res => {
        if (res) {
          this.loader.open();
          this.filterService.delete(id, type)
            .subscribe(data => {
              this.loader.close();
              this.snack.open('Filter is Deleted', 'OK', { duration: 4000 });
              this.loadFilters()
            })
        }
      })
  }

  openPopUp(data: any = {}, isNew?, type?) {
    let title = isNew ? 'Add New Filter' : 'Update Filter';
    let dialogRef: MatDialogRef<any> = this.dialog.open(FilterPopupComponent, {
      disableClose: true,
      data: { title: title, payload: data }
    })
    dialogRef.afterClosed()
      .subscribe(res => {
        if(!res) {
          // If user press cancel
          return;
        }
        this.loader.open();
        if (isNew) {
          
          this.filterService.add(type, res)
            .subscribe(data => {
              this.loader.close();
              this.snack.open('Filter Added!', 'OK', { duration: 4000 })
              this.loadFilters();
            })
        } else {
          
          this.filterService.edit(data.id, res, type)
            .subscribe(data => {
              this.loader.close();
              this.snack.open('Filter Updated!', 'OK', { duration: 4000 })
              this.loadFilters();
            })
        }
      })
    }
}



