import { Component, OnInit } from '@angular/core';
import { MatSnackBar } from '@angular/material';

import { DashboardService } from '../../../../shared/services/admin/dashboard.service';
import { AppConfirmService } from '../../../../shared/services/app-confirm/app-confirm.service';
import { AppLoaderService } from '../../../../shared/services/app-loader/app-loader.service';

@Component({
  selector: 'app-homesliders',
  templateUrl: './homesliders.component.html',
  styleUrls: ['./homesliders.component.scss']
})
export class AppHomeSlidersComponent implements OnInit {

  file;
  isUploading = false;
  sliders;
  
  listStyle = {
    width:'100%', //width of the list defaults to 300
  }

  constructor(
    private dashboardService: DashboardService,
    private confirmService: AppConfirmService,
    private loader: AppLoaderService,
    private snack: MatSnackBar
  ) { }

  ngOnInit() {
    this.loadSliders();
  }

  openDlg() {
    var input = document.getElementById('new-slider-input');
    input.click();
  }

  onFileChange(event) {
    event.preventDefault();
    if (event.target.files.length > 0) {
      this.file = event.target.files[0];
      var reader = new FileReader();
      reader.onload = function(e: any) {
        var previewImg : any = document.getElementById('preview-slider-image');
        previewImg.src = e.target.result;
      }
      reader.readAsDataURL(this.file);
    } else {
      this.file = null;
    }
  }

  upload() {
    var me = this;
    this.isUploading = true;

    this.dashboardService.uploadMusicToS3(this.file).on('httpUploadProgress', function (evt) {
        console.log(evt.loaded + ' of ' + evt.total + ' Bytes');
    }).send(function (err, data) {
      
      if (err) {
          console.log('There was an error uploading your file: ', err);
          return false;
      }
      console.log('Successfully uploaded file.', data);

      me.addNew(data['Location']);
      me.file = null;
      return true;
    });
  }

  addNew(url) {
    var data = {
      slider_img: url
    }
    this.dashboardService.addNewHomeSlider(data).subscribe(data => {
      this.isUploading = false;
      this.loadSliders();
    })
  }

  loadSliders() {
    this.dashboardService.getAllSliders().subscribe(data => {
      this.sliders = data;
    })
  }

  delete(slider_id) {
    this.confirmService.confirm({message: `Delete?`})
      .subscribe(res => {
        if (res) {
          this.loader.open();
          this.dashboardService.removeSlider(slider_id)
            .subscribe(data => {
              this.loader.close();
              this.snack.open('Slider deleted!', 'OK', { duration: 5000 })
              this.loadSliders()
            })
        }
      })
  }

  toggle(slider_id) {
    this.dashboardService.toggleShowSlider(slider_id)
    .subscribe(data => {
      this.snack.open('Slider status Chnaged!', 'OK', { duration: 5000 })
      this.loadSliders()
    })
  }

  listOrderChanged(orderedList) {
    var data = [];
    for(var i = 0; i<orderedList.length; i++) {
      data.push({slider_id: orderedList[i].slider_id, slider_order: i});
    }

    this.dashboardService.changeOrdersSlider(data).subscribe(result => {
      this.snack.open('Slider order Chnaged!', 'OK', { duration: 5000 })
      this.loadSliders()
    })
  }
}



