import { Component, OnInit } from '@angular/core';
import { MatDialogRef, MatDialog, MatSnackBar } from '@angular/material';

import { AppConfirmService } from '../../../shared/services/app-confirm/app-confirm.service';
import { AppLoaderService } from '../../../shared/services/app-loader/app-loader.service';
import { NewsTablePopupComponent } from './newspopup/news-table-popup.component';

import { UserService } from '../../../shared/services/admin/users.service';
import { NewsService } from '../../../shared/services/admin/news.service';

@Component({
  selector: 'app-adminnews',
  templateUrl: './app-adminnews.component.html',
  styleUrls: ['./app-adminnews.component.scss']
})
export class AppAdminNewsComponent implements OnInit {

  items: any[];

  constructor(
    private dialog: MatDialog,
    private snack: MatSnackBar,
    private confirmService: AppConfirmService,
    private loader: AppLoaderService,
    private userService: UserService,
    private newService: NewsService
  ) {
    
  }

  ngOnInit() {
    this.getItems()
  }
  ngOnDestroy() {
  
  }
  getItems() {
    this.newService.getAll().subscribe((data : any)=> {
      this.items = data;
    });
  }

  openPopUp(data: any = {}, isNew?) {
    let title = isNew ? 'Add News' : 'Update News';
    let dialogRef: MatDialogRef<any> = this.dialog.open(NewsTablePopupComponent, {
      // width: '720px',
      height: '600px',
      disableClose: true,
      data: { title: title, payload: data }
    })
    dialogRef.afterClosed()
      .subscribe(res => {
        if(!res) {
          // If user press cancel
          return;
        }
        this.loader.open();
        if (isNew) {
          this.newService.addItem(res)
            .subscribe(data => {
              this.loader.close();
              this.snack.open('News Added!', 'OK', { duration: 4000 })
              this.getItems();
            })
        } else {
          if(res.news_status) {
            res.news_status = '0';
          } else {
            res.news_status = '1';
          }
          this.newService.updateItem(data.news_id, res)
            .subscribe(data => {
              this.loader.close();
              this.snack.open('News Updated!', 'OK', { duration: 4000 })
              this.getItems();
            })
        }
      })
  }
  
  deleteItem(row) {
    this.confirmService.confirm({message: `Delete ${row.news_title}?`})
      .subscribe(res => {
        if (res) {
          this.loader.open();
          this.newService.removeItem(row.news_id)
            .subscribe(data => {
              this.loader.close();
              this.snack.open('News deleted!', 'OK', { duration: 4000 });
              this.getItems();
            })
        }
      })
  }

  changeFeature(row) {
    this.newService.changeFeature(row.news_id).subscribe((data: any) => {
      // row.is_featured = data.is_featured 
      this.getItems();
    })
  }
}



