import { Routes } from '@angular/router';
import { AppDashboardComponent } from './dashbaord/app-dashboard.component';
import { AppAdminArtistsComponent } from './artists/app-adminartists.component';
import { AppAdminUsersComponent } from './user/app-adminusers.component';
import { AppAdminNewsComponent } from './news/app-adminnews.component';
import { AppAdminPlaylistComponent } from './playlist/app-adminplaylist.component';
import { AppAdminSongsComponent } from './songs/app-adminsongs.component';
import { AppAdminSongComponent } from './songs/song/app-adminsong.component';
import { AppAdminAboutComponent } from './about/app-adminabout.component';
import { AppAdminPlacementComponent } from './placement/app-adminplacement.component';

export const AdminRoutes: Routes = [
  {
    path: 'dashboard',
    component: AppDashboardComponent,
    data: { title: 'Admin', breadcrumb: 'Dashboard'}
  },
  {
    path: 'artists',
    component: AppAdminArtistsComponent,
    data: { title: 'Admin', breadcrumb: 'Artists'}
  },
  {
    path: 'users',
    component: AppAdminUsersComponent,
    data: { title: 'Admin', breadcrumb: 'Users'}
  },
  {
    path: 'news',
    component: AppAdminNewsComponent,
    data: { title: 'Admin', breadcrumb: 'News'}
  },
  {
    path: 'playlist',
    component: AppAdminPlaylistComponent,
    data: { title: 'Admin', breadcrumb: 'Playlist'}
  },
  {
    path: 'songs',
    component: AppAdminSongsComponent,
    data: { title: 'Admin', breadcrumb: 'Songs'}
  },
  {
    path: 'song/:song_id',
    component: AppAdminSongComponent,
    data: { title: 'Admin', breadcrumb: 'Songs'}
  },
  {
    path: 'about',
    component: AppAdminAboutComponent,
    data: { title: 'Admin', breadcrumb: 'About'}
  },
  {
    path: 'placements',
    component: AppAdminPlacementComponent,
    data: { title: 'Admin', breadcrumb: 'Placements'}
  }

];


