import { Component, OnInit } from '@angular/core';
import { Subscription } from 'rxjs/Subscription';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import * as MusicTempo  from 'music-tempo';
import { Select2OptionData } from 'ng2-select2';

import { SongsService } from '../../../../shared/services/admin/songs.service';
import { ArtistService } from '../../../../shared/services/admin/artists.service';
import { PlaylistService } from '../../../../shared/services/admin/playlist.service';
import { FilterService } from '../../../../shared/filter.service';

@Component({
  selector: 'app-adminsong',
  templateUrl: './app-adminsong.component.html',
  styleUrls: ['./app-adminsong.component.scss']
})
export class AppAdminSongComponent implements OnInit {

  public song: any = {};
  public getSongSub: Subscription;
  public getArtistSub: Subscription;
  public getFilterSub: Subscription;
  public getPlaylistSub: Subscription;
  public itemForm: FormGroup;

  artists;
  playlists = [];
  filterOptions
  selected_artist_id;
  // selected_playlist_id;
  song_id;
  avatar_url;
  is_changed_avatar;
  file;

  musicFile;
  preListenMusicSrc;
  isMusicUploading = false;

  musicFileWAV;
  preListenMusicSrcWAV;
  isMusicUploadingWAV = false;

  musicFileInst;
  preListenMusicSrcInst;
  isMusicUploadingInst = false;

  musicFileLyrics;
  preListenMusicSrcLyrics;
  isMusicUploadingLyrics = false;

  artistListForRIYL:Array<Select2OptionData> = [];
  riylValue: string[];
  riylOptions : Select2Options;

  playlistValue = [];

  constructor(
    private songsService: SongsService,
    private fb: FormBuilder,
    private artistService: ArtistService,
    private filterService: FilterService,
    private playlistService: PlaylistService,
    private activedRoute: ActivatedRoute,
    private router: Router
  ) {
    
  }

  ngOnInit() {

    this.riylOptions = {
      multiple: true
    }
    
    this.song_id = this.activedRoute.snapshot.params['song_id'];

    if(this.song_id != 'new') {
      this.loadSong();
    } else {
      this.buildItemForm();  
    }

    this.loadArtists();
    this.loadFilters();
    this.loadPlaylists();
  }

  buildItemForm() {
    this.avatar_url = this.song.song_thumb || '';
    this.preListenMusicSrc = this.song.song_music || '';
    this.preListenMusicSrcWAV = this.song.song_music_wav || '';
    this.preListenMusicSrcInst = this.song.song_music_inst || '';
    this.preListenMusicSrcLyrics = this.song.song_music_lyrics || '';
    this.itemForm = this.fb.group({
      song_thumb: [this.song.song_thumb || ''],
      song_music: [this.song.song_music || '', Validators.required],
      song_music_wav: [this.song.song_music_wav || ''],
      song_music_inst: [this.song.song_music_inst || ''],
      song_music_lyrics: [this.song.song_music_lyrics],
      song_title: [this.song.song_title|| '', Validators.required],
      song_performedby: [this.song.song_performedby],
      song_duration: [this.song.song_duration],
      song_bpm: [this.song.song_bpm],
      song_artist: [this.song.song_artist],
      song_mood: [this.song.song_mood],
      song_genre: [this.song.song_genre],
      song_pace: [this.song.song_pace],
      song_instrument: [this.song.song_instrument],
      song_key: [this.song.song_key],
      song_playlist: [this.song.song_playlist],
      song_vocals_inst: [this.song.song_vocals_inst || 'all'],
      song_key_writers: [this.song.song_key_writers || []],
      input_writer: [""],
      song_album_name: [this.song.song_album_name || ''],
      song_release: [this.song.song_release],
      song_original_cover: [this.song.song_original_cover == 1? true : false],
      song_explicit: [this.song.song_explicit == 1 ? true : false],
      song_language: [this.song.song_language],
      song_comments_notes: [this.song.song_comments_notes],
      song_status: [this.song.song_status == 1 ? true : false],
      song_riyl: [this.song.song_riyl],
      song_covers: [this.song.song_covers || '']
    })

    if(this.song_id != 'new' && this.song.song_artist) {
      this.selected_artist_id = this.song.song_artist;
      this.getArtistSub = this.artistService.getAll().subscribe(data => {
        this.artists = data;
        this.artists.map(artist => {
          if(artist.artist_id == this.song.song_artist) {
            this.itemForm.controls['song_artist'].setValue(artist.artist_name)
          }
        })
      })
      
    }

    // if(this.song_id != 'new' && this.song.song_playlist) {
    //   this.selected_playlist_id = this.song.song_playlist;
    //   this.getPlaylistSub = this.playlistService.getAll().subscribe(data => {
    //     this.playlists = data;
    //     this.playlists.map(playlist => {
    //       if(playlist.playlist_id == this.song.song_playlist) {
    //         this.itemForm.controls['song_playlist'].setValue(playlist.playlist_title)
    //       }
    //     })
    //   })
      
    // }
  }
  
  ngOnDestroy() {
    if (this.getSongSub) {
      this.getSongSub.unsubscribe()
    }

    if (this.getArtistSub) {
      this.getArtistSub.unsubscribe()
    }

    if (this.getFilterSub) {
      this.getFilterSub.unsubscribe()
    }

    if (this.getPlaylistSub) {
      this.getPlaylistSub.unsubscribe()
    }
  }

  loadSong() {
    this.getSongSub = this.songsService.getItem(this.song_id)
      .subscribe((data: any) => {
        this.song = data;
        this.buildItemForm();
        if(this.song.song_riyl) {
          this.riylValue = this.song.song_riyl.split(',');
        }

        if(this.song.song_playlist) {
          this.playlistValue = this.song.song_playlist.split(',');
        }
        
      })
  }

  loadArtists() {
    this.getArtistSub = this.artistService.getAll().subscribe(data => {
      this.artists =  data;
      this.artists.map(artist => {
        this.artistListForRIYL.push({id: artist.artist_id, text: artist.artist_name})
      });
    })
  }

  loadPlaylists() {
    this.getPlaylistSub = this.playlistService.getAll().subscribe((data:any) => {
      // this.playlists = data;
      this.playlists = [];
      data.map(playlist => {
        this.playlists.push({id: playlist.playlist_id, text: playlist.playlist_title});
      })
    })
  }

  loadFilters() {
    this.getFilterSub = this.filterService.getAll().subscribe(data => {
      this.filterOptions = data;
    })
  }

  onSelectedArtist(e) {
    this.selected_artist_id = e.option.value;
    this.artists.map(artist => {
      if(artist.artist_id == e.option.value) {
        this.itemForm.controls['song_artist'].setValue(artist.artist_name)
      }
    })
  }

  // onSelectedPlaylist(e) {
  //   this.selected_playlist_id = e.option.value;
  //   this.playlists.map(playlist => {
  //     if(playlist.playlist_id == e.option.value) {
  //       this.itemForm.controls['song_playlist'].setValue(playlist.playlist_title)
  //     }
  //   })
  // }

  submit() {
    if(this.selected_artist_id) {
      this.itemForm.controls['song_artist'].setValue(this.selected_artist_id)
    }

    // if(this.selected_playlist_id) {
    //   this.itemForm.controls['song_playlist'].setValue(this.selected_playlist_id)
    // }
    
    if(this.song_id == 'new') {
      this.songsService.add(this.itemForm.value).subscribe(data => {
        this.router.navigate(['/admin/songs']);
      })
    } else {
      this.songsService.update(this.song_id, this.itemForm.value).subscribe(data => {
        this.router.navigate(['/admin/songs']);
      })
    }
    
  }

  clickChangeAvatar() {
    document.getElementById('avatar-input').click();
  }

  onFileChange(event) {
    event.preventDefault();
    this.is_changed_avatar = true;
    
    if (event.target.files.length > 0) {
      this.file = event.target.files[0];
      var reader = new FileReader();
      reader.onload = function(e: any) {
        var avatarImage : any = document.getElementById('avatar_img');
        avatarImage.src = e.target.result;
      }
      reader.readAsDataURL(this.file);
    }
  }

  upload() {
    const formData = new FormData();
    formData.append('song_thumb', this.file);

    this.songsService.uploadThumb(formData).subscribe(
      (res: any) => {
        this.is_changed_avatar = false;
        this.itemForm.value.song_thumb = res.url;
        this.itemForm.controls['song_thumb'].setValue( res.url);
      },
      (err) => {
        console.log(err)
      }
    );
  }

  onFileChangeMusic(e) {
    if (!e.target.files.length) {
      this.preListenMusicSrc = "";
      return;
    }
    var context = new AudioContext();
    var reader = new FileReader();
    this.musicFile = e.target.files[0];
    var me = this;
    reader.onload = function(e: any) {
      me.preListenMusicSrc = this.result;
   
    }
    reader.readAsDataURL(e.target.files[0]);

    var readerForBeat = new FileReader();
    readerForBeat.onload = function(e: any) {
      context['component'] = me;
      context.decodeAudioData(e.target.result, me.calcTempo);
    }
    readerForBeat.readAsArrayBuffer(e.target.files[0]);
  }

  calcTempo(buffer) {
    var audioData = [];
    // Take the average of the two channels
    if (buffer.numberOfChannels == 2) {
      var channel1Data = buffer.getChannelData(0);
      var channel2Data = buffer.getChannelData(1);
      var length = channel1Data.length;
      for (var i = 0; i < length; i++) {
        audioData[i] = (channel1Data[i] + channel2Data[i]) / 2;
      }
    } else {
      audioData = buffer.getChannelData(0);
    }
    var mt = new MusicTempo(audioData);
    this['component'].itemForm.controls['song_bpm'].setValue(mt.tempo);
  }

  uploadMusic() {
    var me = this;
    this.isMusicUploading = true;

    this.songsService.uploadMusicToS3(this.musicFile).on('httpUploadProgress', function (evt) {
        console.log(evt.loaded + ' of ' + evt.total + ' Bytes');
    }).send(function (err, data) {
      me.isMusicUploading = false;
      if (err) {
          console.log('There was an error uploading your file: ', err);
          return false;
      }
      console.log('Successfully uploaded file.', data);
      me.itemForm.controls['song_music'].setValue( data['Location']);
      return true;
    });
  }

  onFileChangeMusicWAV(e) {
    if (!e.target.files.length) {
      this.preListenMusicSrcWAV = "";
      return;
    }
    var reader = new FileReader();
    this.musicFileWAV = e.target.files[0];
    var me = this;
    reader.onload = function(e: any) {
      me.preListenMusicSrcWAV = this.result;
    }
    reader.readAsDataURL(e.target.files[0]);
  }

  uploadMusicWAV() {
    var me = this;
    this.isMusicUploadingWAV = true;

    this.songsService.uploadMusicToS3(this.musicFileWAV).on('httpUploadProgress', function (evt) {
        console.log(evt.loaded + ' of ' + evt.total + ' Bytes');
    }).send(function (err, data) {
      me.isMusicUploadingWAV = false;
      if (err) {
          console.log('There was an error uploading your file: ', err);
          return false;
      }
      console.log('Successfully uploaded file.', data);
      me.itemForm.controls['song_music_wav'].setValue( data['Location']);
      return true;
    });
  }

  onFileChangeMusicInst(e) {
    if (!e.target.files.length) {
      this.preListenMusicSrcInst = "";
      return;
    }
    var reader = new FileReader();
    this.musicFileInst = e.target.files[0];
    var me = this;
    reader.onload = function(e: any) {
      me.preListenMusicSrcInst = this.result;
    }
    reader.readAsDataURL(e.target.files[0]);
  }

  uploadMusicInst() {
    var me = this;
    this.isMusicUploadingInst = true;

    this.songsService.uploadMusicToS3(this.musicFileInst).on('httpUploadProgress', function (evt) {
        console.log(evt.loaded + ' of ' + evt.total + ' Bytes');
    }).send(function (err, data) {
      me.isMusicUploadingInst = false;
      if (err) {
          console.log('There was an error uploading your file: ', err);
          return false;
      }
      console.log('Successfully uploaded file.', data);
      me.itemForm.controls['song_music_inst'].setValue( data['Location']);
      return true;
    });
  }

  onFileChangeMusicLyrics(e) {
    if (!e.target.files.length) {
      this.preListenMusicSrcLyrics = "";
      return;
    }
    var reader = new FileReader();
    this.musicFileLyrics = e.target.files[0];
    var me = this;
    reader.onload = function(e: any) {
      me.preListenMusicSrcLyrics = this.result;
    }
    reader.readAsDataURL(e.target.files[0]);
  }

  uploadMusicLyrics() {
    var me = this;
    this.isMusicUploadingLyrics = true;

    this.songsService.uploadMusicToS3(this.musicFileLyrics).on('httpUploadProgress', function (evt) {
        console.log(evt.loaded + ' of ' + evt.total + ' Bytes');
    }).send(function (err, data) {
      me.isMusicUploadingLyrics = false;
      if (err) {
          console.log('There was an error uploading your file: ', err);
          return false;
      }
      console.log('Successfully uploaded file.', data);
      me.itemForm.controls['song_music_lyrics'].setValue( data['Location']);
      return true;
    });
  }

  addWriter() {
    var temp : any[] = this.itemForm.controls['song_key_writers'].value;
    temp.push({name: this.itemForm.controls['input_writer'].value});
    this.itemForm.controls['input_writer'].setValue("");
  }

  deleteWriter(index) {
    var temp : any[] = this.itemForm.controls['song_key_writers'].value;
    temp.splice(index, 1)
  }

  duration(duration) {
    // console.log(duration)
    this.itemForm.controls['song_duration'].setValue(duration);
  }

  onRIYLChange(data: {value: string[]}) {
    this.itemForm.controls['song_riyl'].setValue(data.value.join(','));
  }

  onPlaylistChange(data: {value: string[]}) {
    this.itemForm.controls['song_playlist'].setValue(data.value.join(','));
  }
}



