import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { Subscription } from 'rxjs/Subscription';
import { CustomValidators } from 'ng2-validation';

import { PlacementService } from './../../../../shared/services/admin/placement.service';
import { ArtistService } from '../../../../shared/services/admin/artists.service';

@Component({
  selector: 'app-placement-table-popup',
  templateUrl: './placement-table-popup.component.html',
  styleUrls: ['./placement-table-popup.component.scss']
})
export class PlacementTablePopupComponent implements OnInit {
  public itemForm: FormGroup;
  is_changed_avatar = false;
  avatar_url = '';
  file;
  isUploading = false;

  public getArtistSub: Subscription;
  artists = [];
  artistValue = [];
  artistOption = {
    multiple: true
  };
  // selected_artist_id;

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<PlacementTablePopupComponent>,
    private fb: FormBuilder,
    private placementService: PlacementService,
    private artistService: ArtistService
  ) { }

  ngOnInit() {
    this.loadArtists();
    this.buildItemForm(this.data.payload);
  }
  buildItemForm(item) {
    if(item.placement_artists) {
      this.artistValue = item.placement_artists.split(',')
    }
    
    this.avatar_url = item.placement_poster;
    this.itemForm = this.fb.group({
      placement_poster: [item.placement_poster || ''],
      placement_title: [item.placement_title || '', Validators.required],
      placement_youtube: [item.placement_youtube, [Validators.required, CustomValidators.url]],
      placement_linkto: [item.placement_linkto, [Validators.required, CustomValidators.url]],
      placement_description: [item.placement_description],
      placement_artists: [item.placement_artists]
    })

    // if(this.data.title != 'Add new Placement' && item.placement_artist) {
    //   // this.selected_artist_id = item.placement_artist;
    //   this.getArtistSub = this.artistService.getAll().subscribe(data => {
    //     this.artists = data;
    //     data.map(artist => {
    //       if(artist.artist_id == item.placement_artist) {
    //         this.itemForm.controls['placement_artist'].setValue(artist.artist_name)
    //       }
    //     })
    //   })
      
    // }
  }

  loadArtists() {
    this.getArtistSub = this.artistService.getAll().subscribe((data:any) => {
      // this.artists =  data;
      data.map(artist => {
        this.artists.push({id: artist.artist_id, text: artist.artist_name})
      })
    })
  }

  // onSelectedArtist(e) {
  //   this.selected_artist_id = e.option.value;
  //   this.artists.map(artist => {
  //     if(artist.artist_id == e.option.value) {
  //       this.itemForm.controls['placement_artist'].setValue(artist.artist_name)
  //     }
  //   })
  // }

  submit() {
    // if(this.selected_artist_id) {
    //   this.itemForm.controls['placement_artist'].setValue(this.selected_artist_id)
    // }
    this.dialogRef.close(this.itemForm.value)
  }

  clickChangeAvatar() {
    document.getElementById('avatar-input').click();
  }

  onFileChange(event) {
    this.is_changed_avatar = true;
    event.preventDefault();
    if (event.target.files.length > 0) {
      this.file = event.target.files[0];
      var reader = new FileReader();
      reader.onload = function(e: any) {
        var avatarImage : any = document.getElementById('avatar_img');
        avatarImage.src = e.target.result;
      }
      reader.readAsDataURL(this.file);
    }
  }

  upload() {
   var me = this;
    this.isUploading = true;

    this.placementService.uploadMusicToS3(this.file).on('httpUploadProgress', function (evt) {
      console.log(evt.loaded + ' of ' + evt.total + ' Bytes');
    }).send(function (err, data) {
      me.isUploading = false;
      if (err) {
          console.log('There was an error uploading your file: ', err);
          return false;
      }
      console.log('Successfully uploaded file.', data);
      me.itemForm.controls['placement_poster'].setValue( data['Location']);
      return true;
    });
  }

  onArtistChange(data: {value: string[]}) {
    this.itemForm.controls['placement_artists'].setValue(data.value.join(','));
  }
}



