import { Component, OnInit } from '@angular/core';
import { MatDialogRef, MatDialog, MatSnackBar } from '@angular/material';
import { Subscription } from 'rxjs/Subscription';

import { AppConfirmService } from '../../../shared/services/app-confirm/app-confirm.service';
import { AppLoaderService } from '../../../shared/services/app-loader/app-loader.service';
import { PlaylistService } from '../../../shared/services/admin/playlist.service';
import { PlaylistTablePopupComponent } from './playlistpopup/playlist-table-popup.component';

@Component({
  selector: 'app-adminplaylist',
  templateUrl: './app-adminplaylist.component.html',
  styleUrls: ['./app-adminplaylist.component.scss']
})
export class AppAdminPlaylistComponent implements OnInit {

  public items: any[];
  public getItemSub: Subscription;

  constructor(
    private dialog: MatDialog,
    private snack: MatSnackBar,
    private confirmService: AppConfirmService,
    private loader: AppLoaderService,
    private playlistService: PlaylistService
  ) {
    
  }

  ngOnInit() {
    this.getItems()
  }
  
  ngOnDestroy() {
    if (this.getItemSub) {
      this.getItemSub.unsubscribe()
    }
  }

  getItems() {
    this.getItemSub = this.playlistService.getAll()
      .subscribe((data: any) => {
        this.items = data;
      })
  }

  openPopUp(data: any = {}, isNew?) {
    let title = isNew ? 'Add new Playlist' : 'Update Playlist';
    let dialogRef: MatDialogRef<any> = this.dialog.open(PlaylistTablePopupComponent, {
      width: '720px',
      height: '500px',
      disableClose: true,
      data: { title: title, payload: data }
    })
    dialogRef.afterClosed()
      .subscribe(res => {
        if(!res) {
          // If user press cancel
          return;
        }
        this.loader.open();
        if (isNew) {
          this.playlistService.addItem(res)
            .subscribe(data => {
              this.loader.close();
              this.snack.open('Playlist Added!', 'OK', { duration: 4000 })
              this.getItems();
            })
        } else {
          if(res.playlist_status) {
            res.playlist_status = '1';
          } else {
            res.playlist_status = '0';
          }
          this.playlistService.updateItem(data.playlist_id, res)
            .subscribe(data => {
              this.loader.close();
              this.snack.open('Playlist Updated!', 'OK', { duration: 4000 })
              this.getItems();
            })
        }
      })
  }
  
  deleteItem(row) {
    this.confirmService.confirm({message: `Delete ${row.user_name || row.user_email}?`})
      .subscribe(res => {
        if (res) {
          this.loader.open();
          this.playlistService.removeItem(row.playlist_id)
            .subscribe(data => {
              this.loader.close();
              this.snack.open('Playlist deleted!', 'OK', { duration: 4000 });
              this.getItems();
            })
        }
      })
  }


  changeFeature(row) {
    this.playlistService.changeFeature(row.playlist_id).subscribe((data: any) => {
      // row.is_featured = data.is_featured 
      this.getItems();
    })
  }

}



