import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';

import { AboutService } from '../../../shared/services/admin/about.service';

@Component({
  selector: 'app-adminabout',
  templateUrl: './app-adminabout.component.html',
  styleUrls: ['./app-adminabout.component.scss']
})
export class AppAdminAboutComponent implements OnInit {

  about;
  public itemForm: FormGroup;

  constructor(
    private fb: FormBuilder,
    private aboutService: AboutService
  ) {}

  ngOnInit() {
    this.loadAbout()
  }
  ngOnDestroy() {
  }

  loadAbout() {
    this.aboutService.get().subscribe(data => {
      this.about = data;
      this.buildForm();
    });
    
  }

  buildForm() {
    this.itemForm = this.fb.group({
      about_content: [this.about.about_content || '', Validators.required]
    });
  }

  submit() {
    this.aboutService.save(this.itemForm.value).subscribe(data => {
      this.loadAbout();
    })
  }

  onContentChanged() { }
  onSelectionChanged() { }
}



