import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { CustomValidators } from 'ng2-validation';

import { UserService } from './../../../../shared/services/admin/users.service'
import { LocationService } from './../../../../shared/services/location.service';

@Component({
  selector: 'app-user-table-popup',
  templateUrl: './user-table-popup.component.html',
  styleUrls: ['./user-table-popup.component.scss']
})
export class UserTablePopupComponent implements OnInit {
  public itemForm: FormGroup;
  is_changed_avatar = false;
  avatar_url = '';
  file;

  countries = [];
  selectedCountry;

  states = [];
  selectedState;

  cities = [];
  selectedCity;

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<UserTablePopupComponent>,
    private fb: FormBuilder,
    private userService: UserService,
    private locationService: LocationService
  ) { }

  ngOnInit() {
    this.buildItemForm(this.data.payload)
  }
  
  buildItemForm(item) {
    this.avatar_url = item.user_avatar;
    this.itemForm = this.fb.group({
      user_name: [item.user_name || '', Validators.required],
      user_email: [item.user_email || '', Validators.required],
      user_role: [item.user_role || '1', Validators.required],
      is_verified: [item.is_verified == 0 ? false : true],
      user_avatar: [item.user_avatar],
      user_phone: [item.user_phone || '', CustomValidators.phone('BD')],
      user_company: [item.user_company || ''],
      user_position: [item.user_position || ''],
      user_country: [item.user_country || ''],
      user_state: [item.user_state || ''],
      user_city: [item.user_city || ''],
      user_website_url: [item.user_website_url || '', CustomValidators.url],
      user_twitter_url: [item.user_twitter_url || '', CustomValidators.url],
      user_facebook_url: [item.user_facebook_url || '', CustomValidators.url],
      user_linkedin_url: [item.user_linkedin_url || '', CustomValidators.url],
      user_referred_by: [item.user_referred_by || ''],
    })
  }

  submit() {
    this.dialogRef.close(this.itemForm.value)
  }

  clickChangeAvatar() {
    document.getElementById('avatar-input').click();
  }

  onFileChange(event) {
    this.is_changed_avatar = true;
    event.preventDefault();
    if (event.target.files.length > 0) {
      this.file = event.target.files[0];
      var reader = new FileReader();
      reader.onload = function(e: any) {
        var avatarImage : any = document.getElementById('avatar_img');
        avatarImage.src = e.target.result;
      }
      reader.readAsDataURL(this.file);
    }
  }

  upload() {
    const formData = new FormData();
    formData.append('user_avatar', this.file);

    this.userService.uploadAvatar(formData).subscribe(
      (res: any) => {
        this.is_changed_avatar = false;
        // this.itemForm.value.user_avatar = res.url
        this.itemForm.controls['user_avatar'].setValue( res.url);
      },
      (err) => {
        console.log(err)
      }
    );
  }

  loadCountries() {
    this.locationService.getCountries().subscribe((countries : any) => {
      this.countries = countries;
    })
  }

  onCountryChange(e) {
    this.selectedCountry = this.countries.filter(country => country.id == e.option.value);
    this.itemForm.controls['user_country'].setValue(this.selectedCountry[0].name);
    this.loadStates();
  }

  loadStates() {
    this.locationService.getStates(this.selectedCountry[0].id).subscribe((states: any) => {
      this.states = states;
    })
  }

  onStateChange (e) {
    this.selectedState = this.states.filter(state => state.id == e.option.value);
    this.itemForm.controls['user_state'].setValue(this.selectedState[0].name);
  }

  loadCities() {
    this.locationService.getCities(this.selectedState[0].id).subscribe((cities: any) => {
      this.cities = cities;
    })
  }

  onCityChange (e) {
    this.selectedCity = this.cities.filter(city => city.id == e.option.value);
    this.itemForm.controls['user_city'].setValue(this.selectedCity[0].name);
  }
}



