import { Component, OnInit } from '@angular/core';
import { MatSnackBar } from '@angular/material';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { CustomValidators } from 'ng2-validation';

import { DashboardService } from '../../../../shared/services/admin/dashboard.service';
import { AppLoaderService } from '../../../../shared/services/app-loader/app-loader.service';

@Component({
  selector: 'app-social',
  templateUrl: './social.component.html',
  styleUrls: ['./social.component.scss']
})
export class AppSocialComponent implements OnInit {

  isChanged = false;
  public itemForm: FormGroup;

  constructor(
    private dashboardService: DashboardService,
    private loader: AppLoaderService,
    private snack: MatSnackBar,
    private fb: FormBuilder
  ) { }

  ngOnInit() {
    this.loadSocial();
  }

  loadSocial() {
    this.dashboardService.getSocial().subscribe((data:any) => {
      this.buildItemForm(data);
    })
  }

  buildItemForm(item) {
    this.itemForm = this.fb.group({
      social_applemusic: [item.social_applemusic || '', [Validators.required, CustomValidators.url]],
      social_facebook: [item.social_facebook || '', [Validators.required, CustomValidators.url]],
      social_instagram: [item.social_instagram || '', [Validators.required, CustomValidators.url]],
      social_soundcloud: [item.social_soundcloud || '', [Validators.required, CustomValidators.url]],
      social_spotify: [item.social_spotify || '', [Validators.required, CustomValidators.url]],
      social_twitter: [item.social_twitter || '', [Validators.required, CustomValidators.url]],
      social_youtube: [item.social_youtube || '', [Validators.required, CustomValidators.url]]
    })
  }


  save() {
    this.loader.open();
    this.dashboardService.saveSocial(this.itemForm.value).subscribe(data => {
      this.loader.close();
    })
  }
}



