import { Component, OnInit } from '@angular/core';
import { MatDialogRef, MatDialog, MatSnackBar } from '@angular/material';
import { Subscription } from 'rxjs/Subscription';

import { AppConfirmService } from '../../../shared/services/app-confirm/app-confirm.service';
import { AppLoaderService } from '../../../shared/services/app-loader/app-loader.service';
import { UserService } from '../../../shared/services/admin/users.service';
import { User } from '../../../shared/models/user';
import { UserTablePopupComponent } from './userpopup/user-table-popup.component';

@Component({
  selector: 'app-adminusers',
  templateUrl: './app-adminusers.component.html',
  styleUrls: ['./app-adminusers.component.scss']
})
export class AppAdminUsersComponent implements OnInit {

  public items: User[];
  public getItemSub: Subscription;

  constructor(
    private dialog: MatDialog,
    private snack: MatSnackBar,
    private confirmService: AppConfirmService,
    private loader: AppLoaderService,
    private userService: UserService
  ) {
    
  }

  ngOnInit() {
    this.getItems()
  }
  ngOnDestroy() {
    if (this.getItemSub) {
      this.getItemSub.unsubscribe()
    }
  }
  getItems() {
    this.getItemSub = this.userService.getAll()
      .subscribe(users => {
        this.items = users;
      })
  }

  openPopUp(data: any = {}, isNew?) {
    let title = isNew ? 'Add new user' : 'Update user';
    let dialogRef: MatDialogRef<any> = this.dialog.open(UserTablePopupComponent, {
      width: '720px',
      height: '500px',
      disableClose: true,
      data: { title: title, payload: data }
    })
    dialogRef.afterClosed()
      .subscribe(res => {
        if(!res) {
          // If user press cancel
          return;
        }
        this.loader.open();
        if (isNew) {
          this.userService.add(res)
            .subscribe(data => {
              this.loader.close();
              this.snack.open('User Added!', 'OK', { duration: 4000 })
              this.getItems();
            })
        } else {
          if(res.is_verified) {
            res.is_verified = '1';
          } else {
            res.is_verified = '0';
          }
          this.userService.update(data.user_id, res)
            .subscribe(data => {
              this.loader.close();
              this.snack.open('User Updated!', 'OK', { duration: 4000 })
              this.getItems();
            })
        }
      })
  }
  
  deleteItem(row) {
    this.confirmService.confirm({message: `Delete ${row.user_name || row.user_email}?`})
      .subscribe(res => {
        if (res) {
          this.loader.open();
          this.userService.remove(row.user_id)
            .subscribe(data => {
              this.loader.close();
              this.snack.open('User deleted!', 'OK', { duration: 4000 });
              this.getItems();
            })
        }
      })
  }

}



