import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';

import { NewsService } from './../../../../shared/services/admin/news.service';

@Component({
  selector: 'app-news-table-popup',
  templateUrl: './news-table-popup.component.html',
  styleUrls: ['./news-table-popup.component.scss']
})
export class NewsTablePopupComponent implements OnInit {
  public itemForm: FormGroup;
  is_changed_avatar = false;
  avatar_url = '';
  file;

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<NewsTablePopupComponent>,
    private fb: FormBuilder,
    private newsService: NewsService
  ) { }

  ngOnInit() {
    this.buildItemForm(this.data.payload)
  }
  
  buildItemForm(item) {
    this.avatar_url = item.news_thumb;
    this.itemForm = this.fb.group({
      news_title: [item.news_title || '', Validators.required],
      news_status: [item.news_status == 0 ? true : false],
      news_thumb: [item.news_thumb],
      news_content: [item.news_content || '', Validators.required]
    })
  }

  submit() {
    this.dialogRef.close(this.itemForm.value)
  }

  clickChangeAvatar() {
    document.getElementById('avatar-input').click();
  }

  onFileChange(event) {
    this.is_changed_avatar = true;
    event.preventDefault();
    if (event.target.files.length > 0) {
      this.file = event.target.files[0];
      var reader = new FileReader();
      reader.onload = function(e: any) {
        var avatarImage : any = document.getElementById('avatar_img');
        avatarImage.src = e.target.result;
      }
      reader.readAsDataURL(this.file);
    }
  }

  upload() {
    const formData = new FormData();
    formData.append('news_thumb', this.file);

    this.newsService.uploadNewsThumb(formData).subscribe(
      (res: any) => {
        this.is_changed_avatar = false;
        this.itemForm.value.news_thumb = res.url
      },
      (err) => {
        console.log(err)
      }
    );
  }

  onContentChanged() { }
  onSelectionChanged() { }
}



