import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { ReactiveFormsModule } from '@angular/forms';

import { 
  MatAutocompleteModule,
  MatListModule,
  MatIconModule,
  MatButtonModule,
  MatCardModule,
  MatMenuModule,
  MatSlideToggleModule,
  MatGridListModule,
  MatChipsModule,
  MatCheckboxModule,
  MatRadioModule,
  MatTabsModule,
  MatInputModule,
  MatProgressBarModule,
  MatExpansionModule,
  MatDialogModule,
  MatSelectModule,
  MatProgressSpinnerModule,
  MatDatepickerModule,
  MatNativeDateModule
 } from '@angular/material';
import { NgxSortableModule } from 'ngx-sortable';
import { FlexLayoutModule } from '@angular/flex-layout';
import { QuillModule } from 'ngx-quill';
import { Ng5SliderModule } from 'ng5-slider';
import { NgxDatatableModule } from '@swimlane/ngx-datatable';
import { Select2Module } from 'ng2-select2';

import { AppConfirmModule } from '../../shared/services/app-confirm/app-confirm.module';
import { AppLoaderModule } from '../../shared/services/app-loader/app-loader.module';
import { SharedModule } from './../../shared/shared.module';


import { AppDashboardComponent } from './dashbaord/app-dashboard.component';
import { AppAdminArtistsComponent } from './artists/app-adminartists.component';
import { ArtistTablePopupComponent } from './artists/artistpopup/artist-table-popup.component';

import { AppAdminUsersComponent } from './user/app-adminusers.component';
import { UserTablePopupComponent } from './user/userpopup/user-table-popup.component';

import { AppAdminNewsComponent } from './news/app-adminnews.component';
import { NewsTablePopupComponent } from './news/newspopup/news-table-popup.component';

import { AppAdminPlaylistComponent } from './playlist/app-adminplaylist.component';
// import { PlaylistTablePopupComponent } from './playlist/playlistpopup/playlist-table-popup.component';

import { AppAdminSongsComponent } from './songs/app-adminsongs.component';
import { AppAdminSongComponent } from './songs/song/app-adminsong.component';

import { AppAdminAboutComponent } from './about/app-adminabout.component';

import { AppHomeSlidersComponent } from './dashbaord/homesliders/homesliders.component';

import { AppAdminVideosComponent } from './dashbaord/videos/videos.component';
import { VideoTablePopupComponent } from './dashbaord/videos/videopopup/video-table-popup.component';

import { AppAdminPlacementComponent } from './placement/app-adminplacement.component';
import { PlacementTablePopupComponent } from './placement/placementpopup/placement-table-popup.component';

import { AppMusicReviewsComponent } from './dashbaord/musicreviews/musicreviews.component';
import { AppSocialComponent } from './dashbaord/social/social.component';
import { AppFilterComponent } from './dashbaord/filter/filter.component';
import { FilterPopupComponent } from './dashbaord/filter/filterpopup/filterpopup.component';

import { AdminRoutes } from "./admin.routing";

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    MatAutocompleteModule,
    MatListModule,
    MatIconModule,
    MatButtonModule,
    MatCardModule,
    MatMenuModule,
    MatSlideToggleModule,
    MatGridListModule,
    MatChipsModule,
    MatCheckboxModule,
    MatRadioModule,
    MatTabsModule,
    MatInputModule,
    MatProgressBarModule,
    MatExpansionModule,
    MatSelectModule,
    MatProgressSpinnerModule,
    MatDatepickerModule,
    MatNativeDateModule,
    FlexLayoutModule,
    SharedModule,
    RouterModule.forChild(AdminRoutes),
    Ng5SliderModule,
    NgxDatatableModule,
    ReactiveFormsModule,
    MatDialogModule,
    AppConfirmModule,
    AppLoaderModule,
    QuillModule,
    NgxSortableModule,
    Select2Module
  ],
  declarations: [
    AppDashboardComponent,
    AppAdminArtistsComponent,
    ArtistTablePopupComponent,
    AppAdminUsersComponent,
    UserTablePopupComponent,
    AppAdminNewsComponent,
    NewsTablePopupComponent,
    AppAdminPlaylistComponent,
    // PlaylistTablePopupComponent,
    AppAdminSongsComponent,
    AppAdminSongComponent,
    AppAdminAboutComponent,
    AppHomeSlidersComponent,
    AppAdminVideosComponent,
    VideoTablePopupComponent,
    AppAdminPlacementComponent,
    PlacementTablePopupComponent,
    AppMusicReviewsComponent,
    AppSocialComponent,
    AppFilterComponent,
    FilterPopupComponent
  ],
  providers: [
    
  ],
  entryComponents: [
    ArtistTablePopupComponent,
    UserTablePopupComponent,
    NewsTablePopupComponent,
    // PlaylistTablePopupComponent,
    VideoTablePopupComponent,
    PlacementTablePopupComponent,
    FilterPopupComponent
  ]
})
export class AdminModule { }



