import { Component, OnInit } from '@angular/core';
import { MatSnackBar } from '@angular/material';

import { DashboardService } from '../../../../shared/services/admin/dashboard.service';
import { AppLoaderService } from '../../../../shared/services/app-loader/app-loader.service';

@Component({
  selector: 'app-musicreviews',
  templateUrl: './musicreviews.component.html',
  styleUrls: ['./musicreviews.component.scss']
})
export class AppMusicReviewsComponent implements OnInit {

  content
  isChanged = false;
  musicReviewThumb = '';
  file;
  isUploading = false;

  constructor(
    private dashboardService: DashboardService,
    private loader: AppLoaderService,
    private snack: MatSnackBar
  ) { }

  ngOnInit() {
    this.loadMusicReview()
  }

  loadMusicReview() {
    this.dashboardService.getMusicReview().subscribe((data:any) => {
      this.content = data.musicreview_content;
      this.musicReviewThumb = data.musicreview_thumb;
    })
  }

  save() {
    console.log(this.content)
    var data = {
      musicreview_content: this.content,
      musicreview_thumb: this.musicReviewThumb
    }

    this.dashboardService.saveMusicReview(data).subscribe(data => {
      this.loadMusicReview();
    })
  }

  onContentChanged(){
    this.isChanged = true;
  }
  
  onSelectionChanged(){}

  onFileChange(event) {
    event.preventDefault();
    if (event.target.files.length > 0) {
      this.file = event.target.files[0];
      var reader = new FileReader();
      reader.onload = function(e: any) {
        var previewImg : any = document.getElementById('preview-thumb-image');
        previewImg.src = e.target.result;
      }
      reader.readAsDataURL(this.file);
    } else {
      this.file = null;
    }
  }

  upload() {
    var me = this;
    this.isUploading = true;

    this.dashboardService.uploadMusicToS3(this.file).on('httpUploadProgress', function (evt) {
        console.log(evt.loaded + ' of ' + evt.total + ' Bytes');
    }).send(function (err, data) {
      
      if (err) {
          console.log('There was an error uploading your file: ', err);
          return false;
      }
      console.log('Successfully uploaded file.', data);

      me.musicReviewThumb = data['Location'];
      me.file = null;
      me.isUploading = false;
      return true;
    });
  }
}



