import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';

import { ArtistService } from './../../../../shared/services/admin/artists.service';

@Component({
  selector: 'app-artist-table-popup',
  templateUrl: './artist-table-popup.component.html',
  styleUrls: ['./artist-table-popup.component.scss']
})
export class ArtistTablePopupComponent implements OnInit {
  public itemForm: FormGroup;

  is_changed_avatar = false;
  file;
  avatar_url = '';
  is_uploading = false;


  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<ArtistTablePopupComponent>,
    private fb: FormBuilder,
    private artistService: ArtistService
  ) { }

  ngOnInit() {
    this.buildItemForm(this.data.payload)
  }
  buildItemForm(item) {
    this.avatar_url = item.artist_avatar;
    this.itemForm = this.fb.group({
      artist_name: [item.artist_name || '', Validators.required],
      artist_status: [item.artist_status == '0' ? true: false],
      artist_bio: [item.artist_bio || ''],
      artist_members: [item.artist_members || []],
      input_member: [""],
      artist_avatar: [item.artist_avatar],
      artist_contact: [item.artist_contact],
      artist_from: [item.artist_from],
      artist_website_url: [item.artist_website_url],
      artist_twitter: [item.artist_twitter],
      artist_facebook: [item.artist_facebook],
      artist_myspace: [item.artist_myspace],
      artist_youtube: [item.artist_youtube],
      artist_itunes: [item.artist_itunes],
      artist_lastfm: [item.artist_lastfm],
      artist_soundcloud: [item.artist_soundcloud],
      artist_bandcamp: [item.artist_bandcamp],
      artist_instagram: [item.artist_instagram],
      artist_spotify: [item.artist_spotify],
      artist_otherurl1: [item.artist_otherurl1],
      artist_otherurl2: [item.artist_otherurl2],
      artist_vocal: [item.artist_vocal],
      artist_genre: [item.artist_genre],
      artist_spotify_embed: [item.artist_spotify_embed]
    })
  }

  submit() {
    var data = {
      artist_name: this.itemForm.value['artist_name'],
      artist_bio: this.itemForm.value['artist_bio'],
      artist_status: this.itemForm.value['artist_status'] ? '0': '1',
      artist_members: this.itemForm.value['artist_members'],
      artist_avatar: this.itemForm.value['artist_avatar'],
      artist_contact: this.itemForm.value['artist_contact'],
      artist_from: this.itemForm.value['artist_from'],
      artist_website_url: this.itemForm.value['artist_website_url'],
      artist_twitter: this.itemForm.value['artist_twitter'],
      artist_facebook: this.itemForm.value['artist_facebook'],
      artist_myspace: this.itemForm.value['artist_myspace'],
      artist_youtube: this.itemForm.value['artist_youtube'],
      artist_itunes: this.itemForm.value['artist_itunes'],
      artist_lastfm: this.itemForm.value['artist_lastfm'],
      artist_soundcloud: this.itemForm.value['artist_soundcloud'],
      artist_bandcamp: this.itemForm.value['artist_bandcamp'],
      artist_instagram: this.itemForm.value['artist_instagram'],
      artist_spotify: this.itemForm.value['artist_spotify'],
      artist_otherurl1: this.itemForm.value['artist_otherurl1'],
      artist_otherurl2: this.itemForm.value['artist_otherurl2'],
      artist_vocal: this.itemForm.value['artist_vocal'],
      artist_genre: this.itemForm.value['artist_genre'],
      artist_spotify_embed: this.itemForm.value['artist_spotify_embed']
    }
    this.dialogRef.close(data)
  }

  clickChangeAvatar() {
    document.getElementById('avatar-input').click();
  }

  onFileChange(event) {
    this.is_changed_avatar = true;
    event.preventDefault();
    if (event.target.files.length > 0) {
      this.file = event.target.files[0];
      var reader = new FileReader();
      reader.onload = function(e: any) {
        var avatarImage : any = document.getElementById('avatar_img');
        avatarImage.src = e.target.result;
      }
      reader.readAsDataURL(this.file);
    }
  }

  upload() {
    this.is_uploading = true;
    var me  = this;
    this.artistService.uploadThumb(this.file).on('httpUploadProgress', function (evt) {
        console.log(evt.loaded + ' of ' + evt.total + ' Bytes');
    }).send(function (err, data) {
      
      if (err) {
          console.log('There was an error uploading your file: ', err);
          me.is_uploading = false;
          return false;
      }
      
      console.log('Successfully uploaded file.', data);
      this.is_changed_avatar = false;
      me.itemForm.controls['artist_avatar'].setValue( data['Location']);
      me.is_uploading = false;
      return true;
    });
  }

  addMember() {
    var temp : any[] = this.itemForm.controls['artist_members'].value;
    temp.push({name: this.itemForm.controls['input_member'].value});
    this.itemForm.controls['input_member'].setValue("");
  }

  deleteMember(index) {
    var temp : any[] = this.itemForm.controls['artist_members'].value;
    temp.splice(index, 1)
  }
}



