import { Component, OnInit } from '@angular/core';
import { Subscription } from 'rxjs/Subscription';
import { MatDialogRef, MatDialog, MatSnackBar } from '@angular/material';

import { ArtistService } from '../../../shared/services/admin/artists.service';
import { AppConfirmService } from '../../../shared/services/app-confirm/app-confirm.service';
import { AppLoaderService } from '../../../shared/services/app-loader/app-loader.service';
import { ArtistTablePopupComponent } from './artistpopup/artist-table-popup.component';


@Component({
  selector: 'app-adminartists',
  templateUrl: './app-adminartists.component.html',
  styleUrls: ['./app-adminartists.component.scss']
})
export class AppAdminArtistsComponent implements OnInit {

  public items: any[];
  public getItemSub: Subscription;

  listStyle = {
    width:'100%', //width of the list defaults to 300
  }

  constructor(
    private dialog: MatDialog,
    private snack: MatSnackBar,
    private artistService: ArtistService,
    private confirmService: AppConfirmService,
    private loader: AppLoaderService
  ) { }

  ngOnInit() {
    this.getItems()
  }
  ngOnDestroy() {
    if (this.getItemSub) {
      this.getItemSub.unsubscribe()
    }
  }
  getItems() {
    this.getItemSub = this.artistService.getAll()
      .subscribe(data => {
        this.items = data;
      })
  }

  openPopUp(data: any = {}, isNew?) {
    let title = isNew ? 'Add new Artist' : 'Update Artist';
    let dialogRef: MatDialogRef<any> = this.dialog.open(ArtistTablePopupComponent, {
      width: '720px',
      height: '500px',
      disableClose: true,
      data: { title: title, payload: data }
    })
    dialogRef.afterClosed()
      .subscribe(res => {
        if(!res) {
          // If user press cancel
          return;
        }
        this.loader.open();
        if (isNew) {
          this.artistService.addItem(res)
            .subscribe((data: any) => {
              
              this.loader.close();
              this.snack.open('Artist Added!', 'OK', { duration: 4000 })
              this.getItems();
            })
        } else {
          this.artistService.updateItem(data.artist_id, res)
            .subscribe((data: any) => {
              this.loader.close();
              this.snack.open('Artist Updated!', 'OK', { duration: 4000 })
              this.getItems();
            })
        }
      })
  }
  
  deleteItem(row) {
    this.confirmService.confirm({message: `Delete ${row.name}?`})
      .subscribe(res => {
        if (res) {
          this.loader.open();
          this.artistService.removeItem(row.artist_id)
            .subscribe(data => {
              this.loader.close();
              this.snack.open('Artist deleted!', 'OK', { duration: 4000 })
              this.getItems();
            })
        }
      })
  }

  changeFeature(row) {
    this.artistService.changeFeature(row.artist_id).subscribe((data: any) => {
      // row.is_featured = data.is_featured 
      this.getItems();
    })
  }

  listOrderChanged(orderedArtists) {
    var data = [];
    for(var i = 0; i<orderedArtists.length; i++) {
      data.push({artist_id: orderedArtists[i].artist_id, artist_order: i});
    }

    this.artistService.changeOrders(data).subscribe(result => {
      this.getItems();
    })
  }
}



